import React, { useEffect, useState } from "react";
import { SERVER_URL } from "../constants";
import { DataGrid, gridClasses, GridToolbarContainer, GridToolbarExport } from '@mui/x-data-grid';
import { Snackbar, Stack, IconButton, Button } from "@mui/material";
import DeleteIcon from '@mui/icons-material/Delete';
import AddCar from "./AddCar";
import EditCar from "./EditCar";

function CustomToolbar() {
    return (
        <GridToolbarContainer className={gridClasses.toolbarContainer}>
            <GridToolbarExport/>
        </GridToolbarContainer>
    );
};

function Carlist({logout}) {
    const [cars, setCars] = useState([]);
    const [open, setOpen] = useState(false);

    const columns = [
        { field:'brand', headerName:'Brand', width:200 },
        { field:'model', headerName:'Model', width:200 },
        { field:'color', headerName:'Color', width:200 },
        { field:'year', headerName:'Year', width:150 },
        { field:'price', headerName:'Price', width:150 },
        {
            field:'_link.car.href',
            headerName:'Edit',
            sortable:false,
            filterable:false,
            renderCell:row =>
                        <EditCar
                          data={row}
                          updateCar={updateCar}
                        />
        },
        {
            field:'_link.self.href',
            headerName:'Delete',
            sortable:false,
            filterable:false,
            renderCell:row =>
                        <IconButton onClick={() => onDelClick(row.id)}>
                            <DeleteIcon color="error"/>
                        </IconButton>
        }
    ];

    const fetchCars = () => {
        const token = sessionStorage.getItem("jwt");

        fetch(SERVER_URL + 'api/cars', {
            headers: { 'Authorization' : token }
        })
        .then(response => response.json())
        .then(data => setCars(data._embedded.cars))
        .catch(err => console.error(err));
    };
    const onDelClick = (url) => {
        if(window.confirm("정말 삭제하실건가요?")){
            const token = sessionStorage.getItem("jwt");

            fetch(url, {
                method:'DELETE',
                headers: { 'Authorization' : token }
            })
            .then(response => {
                if(response.ok){
                    fetchCars();
                    setOpen(true);
                } else {
                    alert("문제가 발생했습니다!");
                }
            })
            .catch(err => console.error(err));
        }
    };
    // 새 차 추가하기
    const addCar = (car) => {
        const token = sessionStorage.getItem("jwt");

        fetch(SERVER_URL + 'api/cars',
            { 
              method:'POST', 
              headers:{
                'Content-Type':'application/json',
                'Authorization' : token
              },
              body:JSON.stringify(car)
            }
        )
        .then(response => {
            if(response.ok){
                fetchCars();
            } else {
                alert("문제가 발생했습니다!");
            }
        })
        .catch(err => console.error(err))
    };
    // 차 정보 업데이트
    const updateCar = (car, link) => {
        const token = sessionStorage.getItem("jwt");

        fetch(link,
            {
              method:'PUT',
              headers:{
                'Content-Type':'application/json',
                'Authorization' : token
              },
              body:JSON.stringify(car)
            }
        )
        .then(response => {
            if(response.ok){
                fetchCars();
            } else {
                alert("문제가 발생했습니다!");
            }
        })
        .catch(err => console.error(err))
    }

    useEffect(() => {
        fetchCars();
    }, []);


    return (
        <React.Fragment>
            <Button onClick={logout}>로그아웃</Button>
            <Stack mt={2} mb={2}>
                <AddCar addCar={addCar}/>
            </Stack>
            <div style={{ height:500, width:'100%'}}>
                <DataGrid
                rows={cars}
                columns={columns}
                disableRowSelectionOnClick={true}
                getRowId={row => row._links.self.href}
                slots={{toolbar:CustomToolbar}}
                />
                <Snackbar
                open={open}
                autoHideDuration={2000}
                onClose={() => setOpen(false)}
                message="Car Deleted"
                />
            </div>
        </React.Fragment>
    );
};

export default Carlist;